import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/views/login'
import Layout from '@/layout/layout'

Vue.use(Router)

/**
 * alwaysShow: true           if set true, will always show the root menu, whatever its child routes length
 *                            if not set alwaysShow, only more than ont route under the children
 *                            it will becomes nested mode, otherwise not show the root menu
 * alwaysShow: true           true,1
 *                            
 */

export const constantRouterMap = [
  {
    path: '/',
    name: 'login',
    // hidden: true,
    component: Login,
    // redirect: '/login',
    meta: {
      title: ''
    }
  },
  {
    path: '/home',
    hidden: true,
    component: Layout,
    //redirect: '/home',
    children: [
      {
        path: 'home',
        name: 'home',
        component: () => import('@/views/main-item/blank'),
        meta: {title: ''}
      }
    ]
  },
  {
    path: '/person',
    component: Layout,
    meta: {
      icon: 's-home',
      title: ''
    },
    children: [
      {
        path: 'userList',
        name: 'userList',
        component: () => import('@/views/person/userList'),
        meta: {icon: 'user', title: ''}

      },
      {
        path: 'roleManage',
        name: 'roleManage',
        component: () => import('@/views/person/roleManage'),
        meta: {icon: 's-check', title: ''}
      }
    ]
  },
  {
    path: '/organ',
    component: Layout,
    meta: {
      icon: 'office-building',
      title: ''
    },
    children: [
      {
        path: 'organization',
        name: 'organization',
        component: () => import('@/views/organ/organization'),
        meta: {icon: 'coordinate', title: ''}

      },
      {
        path: 'service',
        name: 'service',
        component: () => import('@/views/organ/service'),
        meta: {icon: 's-promotion', title: ''}
      }
    ]
  },
  {
    path: '/data',
    component: Layout,
    meta: {
      icon: 'document-copy',
      title: ''
    },
    children: [
      {
        path: 'search',
        name: 'search',
        component: () => import('@/views/data/search'),
        meta: {icon: 'search', title: ''}
      },
      {
        path: 'watch',
        name: 'watch',
        component: () => import('@/views/data/watch'),
        meta: {icon: 'monitor', title: ''}
      },
      {
        path: 'normal',
        name: 'normal',
        component: () => import('@/views/data/normal'),
        meta: {icon: 'document-checked', title: ''}
      }
    ]
  },
  {
    path: '/serve',
    component: Layout,
    meta: {
      icon: 's-tools',
      title: ''
    },
    children: [
      {
        path: 'table1',
        name: 'menu1-1',
        component: () => import('@/views/serve/fucList'),
        meta: {icon: 'help', title: ''}

      },
      {
        path: 'tabs1',
        name: 'menu2',
        component: () => import('@/views/serve/interfaceList'),
        meta: {icon: 's-help', title: ''}
      }
    ]
  },
  {
    path: '/article',
    component: Layout,
    meta: {
      icon: 'notebook-2',
      title: ''
    },
   children: [
     {
       path: 'article',
       name: 'article-1',
       component: () => import('@/views/article/article'),
       meta: {icon: 'notebook-2', title: ''}
     }]
  }
]

export default new Router({
  // mode: 'history',  require service support
  // scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

export const asyncRouterMap = []



